.inputbox {
  width: 196px;
  margin: 20px;
  position: relative;
}

.inputbox input {
  box-shadow: none;
  color: #23242a;
  letter-spacing: .05em;
  z-index: 10;
  background: none;
  border: none;
  outline: none;
  width: 100%;
  padding: 20px 10px 10px;
  font-size: 1em;
  transition: all .5s;
  position: relative;
}

.inputbox span {
  color: #8f8f8f;
  letter-spacing: .05em;
  pointer-events: none;
  padding: 20px 10px 10px;
  font-size: 1em;
  transition: all .5s;
  position: absolute;
  left: 0;
}

.inputbox input:valid ~ span, .inputbox input:focus ~ span {
  color: #45f3ff;
  font-size: 0, 75em;
  transform: translateX(-10px)translateY(-34px);
}

.inputbox i {
  pointer-events: none;
  z-index: 9;
  background: #45f3ff;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: all .5s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.inputbox input:valid ~ i, .inputbox input:focus ~ i {
  height: 44px;
}

